.UploadButtonBox {
    height: 200px;
    width: 100%;
    text-align: center;
    float: right;
    background: red;
    transition: 1s;
    overflow:hidden
  }
  
  .UploadedDataBox {
    height: 200px;
    width: 0%;
    text-align: center;
    float: left;
    background: blue;
    transition: 1s;
    overflow:hidden
  }
  
  .UploadDataContainer:hover .UploadedDataBox{
    width: 50%;
  }
  
  .container:hover .UploadButtonBox {
    width: 50%;
  }
  .UploadButtonBox:hover {
    width: 50%!important;
  }
  
  .UploadButtonBox:hover+.UploadedDataBox {
    width: 50%;
  }