/* Optional styles */
html {
	box-sizing: border-box;
	font-family: "YouTube Noto", Roboto, Arial, Helvetica, sans-serif;
	height: 100%;
}

*,
*::before,
*::after {
	box-sizing: inherit;
	margin: 0;
	padding: 0;
}

body {
	height: 100%;
}
h1 {
	color: #00053b;
	text-align: center;
	margin-top: 40px;
}
/* END Optional styles */

.video-container {
	border-radius: 10px;
	margin: 0 auto;
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	
}

